@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600&display=swap);
body,canvas,html{width:100%;height:100%;margin:0px;padding:0px;overflow:hidden}.page{background-color:rgba(161,161,161,0.726);display:flex;flex-direction:row;width:100vw;height:100vh}.coordinate-canvas{position:absolute;top:0;right:0;width:200px;height:100px}.show-controls{position:absolute;left:0px;top:0px;height:50px;width:auto;display:flex;justify-content:center;align-items:center;background-color:#222831da;color:#f1faffb0;padding:10px}.controls{position:absolute;flex-direction:column;text-align:center;justify-items:center;align-items:center;color:#f1faffb0;display:flex}h1{font-size:25px;padding-top:20px;margin-bottom:0px}.controls p{text-align:justify;margin:18px;padding:1px}#style-1::-webkit-scrollbar-track{width:7px}canvas{display:block}#style-1::-webkit-scrollbar{width:8px;background-color:#6661682f}#style-1::-webkit-scrollbar-thumb{border-radius:10px;background-color:#afacac}#style-1::-webkit-scrollbar-thumb:hover{background:#555}.btn-primary{display:flex;flex:1 1;margin:0px 8px;background-color:#3f75a5;border-color:#3f75a5;color:#ffffff;font-size:13px;align-items:center;padding:0px;justify-content:center;white-space:nowrap}.btn-primary:hover{background-color:rgba(63,117,165,0.752941)}.btn-primary:disabled{background-color:#3f75a590;border-color:#3f75a590}.btn-toolbar{display:flex;align-items:center;justify-content:center;margin:10px 10px}.btn-group{display:flex;flex:1 1}.button-container{display:flex;align-items:center;justify-content:center;margin:8px 8px}.threeCanvas{width:100vw;height:100vh}.slider-container{display:flex;flex-direction:row;margin:auto;width:95%;padding-bottom:15px}.slider-label{display:flex;justify-content:center;align-items:center;flex:4 1;font-size:14px}.sliderDiv{display:flex;justify-content:center;align-items:center;flex:12 1}.slider-value{display:flex;justify-content:center;align-items:center;flex:3 1}.disable-selection{-moz-user-select:none;-ms-user-select:none;-khtml-user-select:none;-webkit-user-select:none;-webkit-touch-callout:none}.slider{-webkit-appearance:none;-moz-appearance:none;appearance:none;width:100%;height:18px;background:#6b6b6b;border-radius:3px;outline:none;opacity:0.7;transition:opacity 0.2s;pointer-events:none}.slider:hover{opacity:1}.slider::-webkit-slider-thumb{-webkit-appearance:none;appearance:none;width:22px;height:25px;background:#3f75a5;border-radius:3px;cursor:pointer;pointer-events:auto}.slider::-moz-range-progress{background:#494444;height:18px}.slider::-moz-range-thumb{width:22px;height:25px;background:#3f75a5;border-radius:3px;cursor:pointer;pointer-events:auto}.slider:disabled{background:#502424;pointer-events:none;opacity:0.3}.positions-container{margin:15px auto;background-color:#6b6b6bd0;width:95%;border-radius:10px}.positions-container p{text-align:justify;margin:5px 10px;padding:1px}.positions-container h4{padding-top:5px}.position-button-container{display:flex;flex-direction:row-reverse;margin:10px auto}@media only screen and (min-width: 990px){.slider-label{font-size:20px}.slider-value{font-size:20px}h1{font-size:40px;padding-bottom:10px;margin-bottom:0px}.controls p{text-align:justify;padding-left:6%;padding-right:6%}.positions-container p{margin:5px 0px}.show-controls:hover{background-color:#222831a0 !important}.btn-toolbar{margin:10px 20px}}

.accordion__section{display:flex;flex-direction:column}.accordion{background-color:#eee;color:#444;cursor:pointer;padding-right:10px;padding-left:5px;display:flex;align-items:center;border:none;outline:none;transition:background-color 0.6s ease;border-radius:2px}.selected-item{background-color:#fcb1b1 !important}.active{background-color:#ccc}.accordion__title{font-family:'Open Sans', sans-serif;font-weight:600;font-size:14px}.accordion__icon{margin-left:auto;transition:transform 0.6s ease}.rotate{transform:rotate(90deg)}.accordion__content{background-color:white;overflow:hidden;transition:max-height 0.6s ease}.accordion__text{width:100%;font-family:'Open Sans', sans-serif;font-weight:400;font-size:14px}@media only screen and (min-width: 990px){.accordion:hover{background-color:#ccc}}

.accordion-item{display:flex;flex-direction:row;width:100%;border:1px solid #000000;color:#000000;border-radius:1px}.accordion-item p{display:flex;flex:2 1;align-items:flex-start;justify-content:center}.accordion-item input{display:flex;flex:1 1;background-color:#cbcbcb}

.label{color:black;display:flex;flex:2 1;text-align:start;padding:0px 5px;border:1px solid #000000;height:31px;align-items:center;white-space:nowrap}.input{display:flex;flex:5 1;width:100%}input[type='number']{text-align:right;padding-right:15px;height:31px;border-radius:0px;width:100%}form{display:flex;justify-content:center;align-items:center;width:100%;font-size:16px}@media only screen and (min-width: 990px){input[type='number']{padding-right:0px}}

body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}*{box-sizing:border-box;margin:0;padding:0}

